<template>
  <q-page>
      <q-card bordered class="q-ma-md" style="margin-top: 90px;">
        <q-card-section >
          <q-card-section>
          <q-btn-toggle
            no-caps
            v-model="panorama"
            rounded
            @click="changePanoramaAbsolut(this.panorama)"
            toggle-color="primary"
            :options="[
              {label: 'Halblinks', value: 25},
              {label: 'Mitte', value: 63},
              {label: 'Halbrechts', value: 102},
            ]"
          ></q-btn-toggle>
          </q-card-section>
        </q-card-section>

      </q-card>
    </q-page>
</template>

<script>
import { useInstrumentStore } from 'stores/instruments-store';
import { getInstrumentById, updateInstrumentPanorama  } from '@/services/apiServices';

export default {
  name: 'PanoramaDialog',
  setup() {
  },
  data() {
    return {
      panorama: 0,
      panoramaToggle: 'center',
      data: null,
      instrumentId: this.$route.params.id,
    };
  },
  computed: {
    intPanorama: {
      get() {
        return this.panorama;
      },
      set(value) {
        let panorama = parseInt(value, 10);
        this.changePanorama(panorama - this.panorama);
      }
    },
  },
  mounted() {
    this.updateInstrumentState(this.instrumentId);
  },
  unmounted() {
  },
  methods: {
    updateInstrumentState(selectedInstrumentId) {
      if (!selectedInstrumentId) return;

      getInstrumentById(selectedInstrumentId)
        .then(response => {
          if (response.data.panorama <= 25) {
            this.panorama= 25;
          }
          else if (response.data.panorama >= 102)
          {
            this.panorama= 102;
          }
          else {
            this.panorama = 63;
          }
        })
        .catch(error => {
          console.error('Error fetching instrument state:', error);
        });
    },
    changePanoramaAbsolut(panorama) {
      this.panorama = panorama;

      updateInstrumentPanorama(this.instrumentId, panorama)
        .then(response => {
            this.panorama = response.data.panorama;
        })
        .catch(error => {
          console.error('Error updating instrument panorama:', error);
        });
    },
    changePanorama(direction) {
      const newPanorma = Math.max(0, Math.min(127, this.panorama + direction));
      this.panorama = newPanorma;

      updateInstrumentPanorama(this.instrumentId, newPanorma)
        .then(response => {
            this.panorama = response.data.panorama;
        })
        .catch(error => {
          console.error('Error updating instrument panorama:', error);
        });
    }
  }
}
</script>

